import React, { useContext, useState, useRef, useReducer } from "react";
import {
  Box,
  TextField,
  Button,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
} from "@mui/material";
import AddIcon from "@mui/icons-material/Add";
import DeleteIcon from "@mui/icons-material/Delete";
import { GridRowSelectionModel } from "@mui/x-data-grid";
import { pink } from "@mui/material/colors";
import { AuthContext } from "../../../components/AuthContext";
import { useMutation, useQuery, useQueryClient } from "react-query";
import Swal from "sweetalert2";
import { wait } from "../../../lib/wait";
import CloseIcon from "@mui/icons-material/Close";
import SaveIcon from "@mui/icons-material/Save";
import Checkbox from "@mui/material/Checkbox";
import FormControlLabel from "@mui/material/FormControlLabel";
import { LoadingButton } from "@mui/lab";
import Table from "../../../components/Table";
import {
  codeCondfirmationAlert,
  saveCondfirmationAlert,
} from "../../../lib/confirmationAlert";

const initialState = {
  Acct_Code: "",
  Acct_Title: "",
  Short: "",
  Account: "Asset",
  Acct_Type: "Group Header",
  IDNo: false,
  SubAccnt: false,
  Inactive: false,
  mode: "",
  search: "",
};
export const reducer = (state: any, action: any) => {
  switch (action.type) {
    case "UPDATE_FIELD":
      return {
        ...state,
        [action.field]: action.value,
      };
    default:
      return state;
  }
};

export const chartAccountColumn = [
  { field: "Acct_Code", headerName: "Account Code", width: 150 },
  { field: "Acct_Title", headerName: "Account Name/Account Title", width: 300 },
  { field: "Short", headerName: "Short Name", width: 300 },
  { field: "Acct_Type", headerName: "Account Type", width: 200 },
  { field: "SubAccnt", headerName: "Sub Account ?", width: 100 },
  { field: "IDNo", headerName: "I.D. ?", width: 100 },
  { field: "Inactive", headerName: "Inactive ?", width: 100 },
];

const queryKey = "chart-account";
export default function ChartAccount() {
  const refParent = useRef<HTMLDivElement>(null);

  const [state, dispatch] = useReducer(reducer, initialState);
  const { myAxios, user } = useContext(AuthContext);
  const [rows, setRows] = useState<GridRowSelectionModel>([]);
  const table = useRef<any>(null);
  const queryClient = useQueryClient();
  const { isLoading, refetch: refetchChartAccountSearch } = useQuery({
    queryKey,
    queryFn: async () =>
      await myAxios.get(
        `/reference/get-chart-accounts?chartAccountSearch=${state.search}`,
        {
          headers: {
            Authorization: `Bearer ${user?.accessToken}`,
          },
        }
      ),
    onSuccess: (res) => {
      setRows((res as any)?.data.chartAccount);
    },
  });
  const { mutate: mutateAdd, isLoading: loadingAdd } = useMutation({
    mutationKey: queryKey,
    mutationFn: async (variables: any) => {
      return await myAxios.post("/reference/add-chart-account", variables, {
        headers: {
          Authorization: `Bearer ${user?.accessToken}`,
        },
      });
    },
    onSuccess,
  });
  const { mutate: mutateEdit, isLoading: loadingEdit } = useMutation({
    mutationKey: queryKey,
    mutationFn: async (variables: any) => {
      return await myAxios.post("/reference/update-chart-account", variables, {
        headers: {
          Authorization: `Bearer ${user?.accessToken}`,
        },
      });
    },
    onSuccess,
  });
  const { mutate: mutateDelete, isLoading: loadingDelete } = useMutation({
    mutationKey: queryKey,
    mutationFn: async (variables: any) => {
      return await myAxios.post("/reference/delete-chart-account", variables, {
        headers: {
          Authorization: `Bearer ${user?.accessToken}`,
        },
      });
    },
    onSuccess,
  });
  const handleInputChange = (e: any) => {
    const { name, value } = e.target;
    dispatch({ type: "UPDATE_FIELD", field: name, value });
  };

  function handleOnSave(e: any) {
    if (state.Acct_Code === "") {
      return Swal.fire({
        position: "center",
        icon: "warning",
        title: "Acct Code is required!",
        showConfirmButton: false,
        timer: 1500,
      });
    }
    if (state.Acct_Title === "") {
      return Swal.fire({
        position: "center",
        icon: "warning",
        title: "Acct Title is required!",
        showConfirmButton: false,
        timer: 1500,
      });
    }
    if (state.Short === "") {
      return Swal.fire({
        position: "center",
        icon: "warning",
        title: "Short Name is required!",
        showConfirmButton: false,
        timer: 1500,
      });
    }

    if (state.Acct_Code.length >= 200) {
      return Swal.fire({
        position: "center",
        icon: "warning",
        title: "Acct Code is too long!",
        showConfirmButton: false,
        timer: 1500,
      });
    }
    if (state.Acct_Title.length >= 200) {
      return Swal.fire({
        position: "center",
        icon: "warning",
        title: "Acct Title is too long!",
        showConfirmButton: false,
        timer: 1500,
      });
    }
    if (state.Short.length >= 200) {
      return Swal.fire({
        position: "center",
        icon: "warning",
        title: "Short Name is too long!",
        showConfirmButton: false,
        timer: 1500,
      });
    }
    e.preventDefault();
    if (state.mode === "edit") {
      codeCondfirmationAlert({
        isUpdate: true,
        cb: (userCodeConfirmation) => {
          mutateEdit({ ...state, userCodeConfirmation });
        },
      });
    } else {
      saveCondfirmationAlert({
        isConfirm: () => {
          mutateAdd(state);
        },
      });
    }
  }

  function resetModule() {
    setNewStateValue(dispatch, initialState);
    table.current?.removeSelection();
    wait(500).then(() => {
      refetchChartAccountSearch();
    });
  }
  function onSuccess(res: any) {
    if (res.data.success) {
      queryClient.invalidateQueries(queryKey);
      resetModule();
      return Swal.fire({
        position: "center",
        icon: "success",
        title: res.data.message,
        showConfirmButton: false,
        timer: 1500,
      });
    }

    Swal.fire({
      position: "center",
      icon: "error",
      title: res.data.message,
      showConfirmButton: false,
      timer: 1500,
    });
  }

  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
        width: "100%",
        height: "100%",
        flex: 1,
      }}
    >
      <Box
        sx={(theme) => ({
          display: "flex",
          alignItems: "center",
          columnGap: "20px",
          [theme.breakpoints.down("sm")]: {
            flexDirection: "column",
            alignItems: "flex-start",
            marginBottom: "15px",
          },
        })}
      >
        <div
          style={{
            marginTop: "10px",
            marginBottom: "12px",
            width: "100%",
          }}
        >
          <TextField
            label="Search"
            fullWidth
            size="small"
            type="text"
            name="search"
            value={state.search}
            onChange={handleInputChange}
            InputProps={{
              style: { height: "27px", fontSize: "14px" },
            }}
            sx={{
              height: "27px",
              ".MuiFormLabel-root": { fontSize: "14px" },
              ".MuiFormLabel-root[data-shrink=false]": { top: "-5px" },
            }}
            onKeyDown={(e) => {
              if (e.code === "Enter" || e.code === "NumpadEnter") {
                e.preventDefault();
                return refetchChartAccountSearch();
              }
            }}
          />
        </div>
        <div
          style={{
            display: "flex",
            alignItems: "center",
            columnGap: "20px",
          }}
        >
          <div
            style={{
              display: "flex",
              alignItems: "center",
              columnGap: "5px",
            }}
          >
            {state.mode === "" && (
              <Button
                sx={{
                  height: "30px",
                  fontSize: "11px",
                }}
                variant="contained"
                startIcon={<AddIcon />}
                id="entry-header-save-button"
                onClick={() => {
                  handleInputChange({ target: { value: "add", name: "mode" } });
                }}
              >
                New
              </Button>
            )}

            <LoadingButton
              sx={{
                height: "30px",
                fontSize: "11px",
              }}
              id="save-entry-header"
              color="primary"
              variant="contained"
              type="submit"
              onClick={handleOnSave}
              disabled={state.mode === ""}
              loading={loadingAdd || loadingEdit}
              startIcon={<SaveIcon />}
            >
              Save
            </LoadingButton>

            {state.mode !== "" && (
              <Button
                sx={{
                  height: "30px",
                  fontSize: "11px",
                }}
                variant="contained"
                startIcon={<CloseIcon />}
                color="error"
                onClick={() => {
                  Swal.fire({
                    title: "Are you sure?",
                    text: "You won't be able to revert this!",
                    icon: "warning",
                    showCancelButton: true,
                    confirmButtonColor: "#3085d6",
                    cancelButtonColor: "#d33",
                    confirmButtonText: "Yes, cancel it!",
                  }).then((result) => {
                    if (result.isConfirmed) {
                      resetModule();
                    }
                  });
                }}
              >
                Cancel
              </Button>
            )}

            <Button
              id="save-entry-header"
              variant="contained"
              sx={{
                height: "30px",
                fontSize: "11px",
                backgroundColor: pink[500],
                "&:hover": {
                  backgroundColor: pink[600],
                },
              }}
              disabled={state.mode !== "edit"}
              startIcon={<DeleteIcon />}
              onClick={() => {
                codeCondfirmationAlert({
                  isUpdate: false,
                  cb: (userCodeConfirmation) => {
                    mutateDelete({
                      Acct_Code: state.Acct_Code,
                      userCodeConfirmation,
                    });
                  },
                });
              }}
            >
              Delete
            </Button>
          </div>
        </div>
      </Box>
      <form
        onKeyDown={(e) => {
          if (e.code === "Enter" || e.code === "NumpadEnter") {
            e.preventDefault();
            handleOnSave(e);
            return;
          }
        }}
      >
        <Box
          sx={(theme) => ({
            width: "100%",
            display: "flex",
            columnGap: "15px",
            flexDirection: "row",
            [theme.breakpoints.down("sm")]: {
              flexDirection: "column",
              rowGap: "10px",
            },
          })}
        >
          {!isLoading && (
            <React.Fragment>
              <TextField
                required
                variant="outlined"
                size="small"
                label="Code"
                name="Acct_Code"
                value={state.Acct_Code}
                onChange={handleInputChange}
                disabled={state.mode === "" || state.mode === "edit"}
                InputProps={{
                  style: { height: "27px", fontSize: "14px" },
                }}
                sx={{
                  height: "27px",
                  ".MuiFormLabel-root": { fontSize: "14px" },
                  ".MuiFormLabel-root[data-shrink=false]": { top: "-5px" },
                }}
              />
              <FormControl
                size="small"
                disabled={state.mode === ""}
                required
                sx={{
                  flex: 1,
                  ".MuiFormLabel-root": {
                    fontSize: "14px",
                    background: "white",
                    zIndex: 99,
                    padding: "0 3px",
                  },
                  ".MuiFormLabel-root[data-shrink=false]": { top: "-5px" },
                }}
              >
                <InputLabel id="Account">Account Classification</InputLabel>
                <Select
                  sx={{
                    height: "27px",
                    fontSize: "14px",
                  }}
                  labelId="Account"
                  value={state.Account}
                  label="Account Classification"
                  name="Account"
                  onChange={handleInputChange}
                >
                  <MenuItem value={"Asset"}>Assets</MenuItem>
                  <MenuItem value={"Liability"}>Liability</MenuItem>
                  <MenuItem value={"Equity"}>Equity</MenuItem>
                  <MenuItem value={"Revenue"}>Revenue</MenuItem>
                  <MenuItem value={"Expense"}>Expense</MenuItem>
                </Select>
              </FormControl>
              <FormControl
                size="small"
                disabled={state.mode === ""}
                required
                sx={{
                  ".MuiFormLabel-root": {
                    fontSize: "14px",
                    background: "white",
                    zIndex: 99,
                    padding: "0 3px",
                  },
                  ".MuiFormLabel-root[data-shrink=false]": { top: "-5px" },
                }}
              >
                <InputLabel id="Acct_Type">Account Type</InputLabel>
                <Select
                  sx={{
                    height: "27px",
                    fontSize: "14px",
                  }}
                  labelId="Acct_Type"
                  value={state.Acct_Type}
                  label="Acct_Type"
                  name="Acct_Type"
                  onChange={handleInputChange}
                >
                  <MenuItem value={"Group Header"}>Group Header</MenuItem>
                  <MenuItem value={"Header"}>Header</MenuItem>
                  <MenuItem value={"Detail"}>Detail</MenuItem>
                </Select>
              </FormControl>
              <TextField
                required
                variant="outlined"
                size="small"
                label="Name/Title"
                name="Acct_Title"
                value={state.Acct_Title}
                onChange={handleInputChange}
                disabled={state.mode === ""}
                InputProps={{
                  style: { height: "27px", fontSize: "14px" },
                }}
                sx={{
                  height: "27px",
                  ".MuiFormLabel-root": { fontSize: "14px" },
                  ".MuiFormLabel-root[data-shrink=false]": { top: "-5px" },
                }}
              />
              <TextField
                required
                variant="outlined"
                size="small"
                label="Short Name"
                name="Short"
                value={state.Short}
                onChange={handleInputChange}
                disabled={state.mode === ""}
                InputProps={{
                  style: { height: "27px", fontSize: "14px" },
                }}
                sx={{
                  height: "27px",
                  ".MuiFormLabel-root": { fontSize: "14px" },
                  ".MuiFormLabel-root[data-shrink=false]": { top: "-5px" },
                }}
              />
            </React.Fragment>
          )}
        </Box>
        <Box
          sx={(theme) => ({
            width: "100%",
            display: "flex",
            columnGap: "5px",
            flexDirection: "row",
            [theme.breakpoints.down("sm")]: {
              rowGap: "5px",
              flexWrap: "wrap",
            },
          })}
        >
          <FormControlLabel
            sx={{
              ".MuiTypography-root": {
                fontSize: "14px",
              },
              width: "200px",
            }}
            control={
              <Checkbox
                size="small"
                name="SubAccnt"
                checked={state.SubAccnt}
                onChange={(e) => {
                  dispatch({
                    type: "UPDATE_FIELD",
                    field: "SubAccnt",
                    value: e.target.checked,
                  });
                }}
              />
            }
            label="Required sub-account?"
          />
          <FormControlLabel
            sx={{
              ".MuiTypography-root": {
                fontSize: "14px",
              },
              width: "150px",
            }}
            control={
              <Checkbox
                size="small"
                name="IDNo"
                checked={state.IDNo}
                onChange={(e) => {
                  dispatch({
                    type: "UPDATE_FIELD",
                    field: "IDNo",
                    value: e.target.checked,
                  });
                }}
              />
            }
            label="Required I.D.?"
          />
          <FormControlLabel
            sx={{
              ".MuiTypography-root": {
                fontSize: "14px",
              },
              width: "150px",
            }}
            control={
              <Checkbox
                size="small"
                name="Inactive"
                checked={state.Inactive}
                onChange={(e) => {
                  dispatch({
                    type: "UPDATE_FIELD",
                    field: "Inactive",
                    value: e.target.checked,
                  });
                }}
              />
            }
            label="Mark As Inactive"
          />
        </Box>
      </form>
      <div
        ref={refParent}
        style={{
          marginTop: "10px",
          width: "100%",
          position: "relative",
          flex: 1,
        }}
      >
        <Box
          style={{
            height: `${refParent.current?.getBoundingClientRect().height}px`,
            width: "100%",
            overflowX: "scroll",
            position: "absolute",
          }}
        >
          <Table
            ref={table}
            isLoading={isLoading || loadingAdd || loadingEdit || loadingDelete}
            columns={chartAccountColumn}
            rows={rows}
            table_id={"Acct_Code"}
            isSingleSelection={true}
            isRowFreeze={false}
            dataSelection={(selection, data, code) => {
              const rowSelected = data.filter(
                (item: any) => item.Acct_Code === selection[0]
              )[0];
              if (rowSelected === undefined || rowSelected.length <= 0) {
                setNewStateValue(dispatch, initialState);
                handleInputChange({ target: { value: "", name: "mode" } });

                return;
              }
              handleInputChange({ target: { value: "edit", name: "mode" } });
              if (code === "Delete" || code === "Backspace") {
                codeCondfirmationAlert({
                  isUpdate: false,
                  cb: (userCodeConfirmation) => {
                    mutateDelete({
                      Acct_Code: rowSelected.Acct_Code,
                      userCodeConfirmation,
                    });
                  },
                });
                return;
              }
              const newState = {
                ...rowSelected,
                Inactive: rowSelected.Inactive !== "NO",
                SubAccnt: rowSelected.SubAccnt !== "NO",
                IDNo: rowSelected.IDNo !== "NO",
              };
              setNewStateValue(dispatch, newState);
            }}
          />
        </Box>
      </div>
    </div>
  );
}
export function setNewStateValue(dispatch: any, obj: any) {
  Object.entries(obj).forEach(([field, value]) => {
    dispatch({ type: "UPDATE_FIELD", field, value });
  });
}
